import {useCallback, useEffect, useState} from 'react';
import {styled} from "@mui/material/styles";
import DasService from "./DasService";
import {Button, Grid, IconButton, Link, TextField, Typography} from "@mui/material";
import {NavigateBefore, NavigateNext} from "@mui/icons-material";
import AceImageViewerV2 from "./AceImageViewerV2";

const RootContainer = styled('div')(({ theme }) => ({
  marginLeft: theme.spacing(4),
  marginRight: theme.spacing(4),
  marginTop: theme.spacing(4),
  marginBottom: theme.spacing(4),
}));

const dasService = new DasService();
const CACHE_KEY = 'gedcomx-differ-session';

function getProdSessionIdHelperText() {
  return (
    <Grid container justifyContent='flex-start' alignItems='center' spacing={1} component='span'>
      <Grid item component='span'>
        <Typography variant='caption'>Get prod session ID:</Typography>
      </Grid>
      <Grid item component='span'>
        <Link href="https://www.familysearch.org/platform/" color="primary" target="_blank">https://www.familysearch.org/platform/</Link>
      </Grid>
    </Grid>
  );
}

function viewPreviousImage(currentNineFive, settingsData, setImage, setCurrentNineFive) {
  const nineFive = currentNineFive.replace('.jpg', '');
  const nine = nineFive.split('_')[0];
  let five = nineFive.split('_')[1];
  five = String(parseInt(five) - 1).padStart(5, '0');
  const previousNineFive = `${nine}_${five}`;
  dasService.loadImage(previousNineFive, settingsData, {imageSetter: setImage, setCurrentNineFive: setCurrentNineFive})
}

function viewNextImage(currentNineFive, settingsData, setImage, setCurrentNineFive) {
  const nineFive = currentNineFive.replace('.jpg', '');
  const nine = nineFive.split('_')[0];
  let five = nineFive.split('_')[1];
  five = String(parseInt(five) + 1).padStart(5, '0');
  const nextNineFive = `${nine}_${five}`;
  dasService.loadImage(nextNineFive, settingsData, {imageSetter: setImage, setCurrentNineFive: setCurrentNineFive})
}

function ResultsDisplay({ image, setImage, settingsData, currentNineFive, setCurrentNineFive }) {
  return (
    <Grid container direction="row" spacing={1} alignItems="center">
      <Grid item xs={1}>
        <IconButton onClick={() => viewPreviousImage(currentNineFive, settingsData, setImage, setCurrentNineFive)}>
          <NavigateBefore/>
        </IconButton>
      </Grid>
      <Grid item xs={10}>
        {image ? <AceImageViewerV2 id={`image-viewer`} image={image}/> : null}
      </Grid>
      <Grid item xs={1}>
        <IconButton onClick={() => viewNextImage(currentNineFive, settingsData, setImage, setCurrentNineFive)}>
          <NavigateNext/>
        </IconButton>
      </Grid>
    </Grid>
  )
}

export default function ImageViewer() {
  const cachedSession = localStorage.getItem(CACHE_KEY) ? localStorage.getItem(CACHE_KEY) : '';
  const [image, setImage] = useState(null);
  const [currentNineFive, setCurrentNineFive] = useState('');  // The nineFive of the image currently in view
  const [settingsData, setSettingsData] = useState({dasHost: 'https://www.familysearch.org', prodSessionId: cachedSession})

  const handleKeyPress = useCallback((event) => {
    const keyCode = event.key;
    if (image && keyCode === 'ArrowLeft') {
      viewPreviousImage(currentNineFive, settingsData, setImage, setCurrentNineFive);
    }
    if (image && keyCode === 'ArrowRight') {
      viewNextImage(currentNineFive, settingsData, setImage, setCurrentNineFive);
    }
  }, [currentNineFive, settingsData, image]);

  useEffect(() => {
    document.addEventListener('keydown', handleKeyPress);
    return () => {
      document.removeEventListener('keydown', handleKeyPress);
    };
  }, [handleKeyPress]);

  useEffect(() => {
    try {
      localStorage.setItem(CACHE_KEY, settingsData.prodSessionId);
    } catch (error) {
      console.error(`Error caching your session id: ${error}`);
    }
  }, [settingsData.prodSessionId]);

  function handleLoadImage() {
    dasService.loadImage(currentNineFive, settingsData, {imageSetter: setImage, setCurrentNineFive: setCurrentNineFive})
  }

  return (
    <RootContainer>
      <Grid container direction="column" spacing={2}>
        <Grid item>
          <Grid container spacing={1} alignItems='center'>
            <Grid item xs={5}>
              <TextField
                label={cachedSession ? `Saved Session (${settingsData.prodSessionId})` : 'Prod Session ID'}
                variant="outlined"
                margin="normal"
                fullWidth={true}
                onChange={e => setSettingsData({dasHost: 'https://www.familysearch.org', prodSessionId: e.target.value})}
                helperText={getProdSessionIdHelperText()}
                required/>
            </Grid>
            <Grid item xs={5}>
              <TextField
                label={`Image 9_5`}
                variant='outlined'
                margin='normal'
                fullWidth={true}
                onChange={e => setCurrentNineFive(e.target.value)}
                helperText={'Example: 004170619_00481'}
                required/>
            </Grid>
            <Grid item xs={2}>
              <Button variant='contained' size='large' onClick={handleLoadImage}>Load Image</Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
          <ResultsDisplay
            image={image}
            setImage={setImage}
            settingsData={settingsData}
            currentNineFive={currentNineFive}
            setCurrentNineFive={setCurrentNineFive}
          />
        </Grid>
      </Grid>
    </RootContainer>
  )
}