class DasService {
  loadImage(imageId, settingsData, setters) {
    console.log(`Loading image ${imageId}`);
    fetch(`${settingsData.dasHost}/das/v2/dgs:${imageId}/dist.jpg?sessionId=${settingsData.prodSessionId}`)
      .then(r => {
        if (!r.ok) {
          const errorMessage = `Error loading DAS artifact ${imageId}`;
          setters.imageSetter(null);
          console.error(errorMessage);
          return Promise.reject(errorMessage);
        } else {
          return r.blob();
        }
      })
      .then(blob => {
        const imageObject = {
          name: `${imageId}.jpg`,
          fileObj: blob
        };
        setters.imageSetter(imageObject);
        setters.setCurrentNineFive(`${imageId}`)
      });
  }
}

export default DasService;
