import {Box, Grid, Typography} from "@mui/material";
import {FileDrop} from "react-file-drop";
import PersonsList from "./PersonsList";
import {RecordsDataContext} from "../RecordsContext";
import {getGxIntersection, leftRecordsData, rightRecordsData} from "../EditPage";
import {useState} from "react";
import {EMPTY_GEDCOMX, FACT_ATTRIBUTES} from "../constants";

function personFieldsAreEqual(fieldsA, fieldsB) {
  return JSON.stringify(fieldsA) === JSON.stringify(fieldsB);
}

function factsAreEqual(factA, factB) {
  const factAKeys = Object.keys(factA);
  const factBKeys = Object.keys(factB);

  if (factAKeys.length !== factBKeys.length) {
    return false;
  }

  return factAKeys.every(factAKey => {
    if (factAKey === FACT_ATTRIBUTES.type || factAKey === FACT_ATTRIBUTES.value || factAKey === FACT_ATTRIBUTES.primary) {
      return factA[factAKey] === factB[factAKey];
    } else if (factAKey === FACT_ATTRIBUTES.date || factAKey === FACT_ATTRIBUTES.place) {
      return factA[factAKey].original === factB[factAKey]?.original;
    } else if (factAKey === FACT_ATTRIBUTES.qualifiers) {
      if (factA.qualifiers && factB.qualifiers) {
        return factA.qualifiers.every(qA => factB.qualifiers.find(qB => JSON.stringify(qA) === JSON.stringify(qB)) !== undefined);
      }
      return !factA.qualifiers && !factB.qualifiers;
    } else if (factAKey === FACT_ATTRIBUTES.id) {
      //ids can be different because this tool is for diffing separately created GedcomX
      return true;
    } else {
      console.error(`Unexpected fact key ${factAKey}`);
      return false;
    }
  });
}

function factIsInFacts(fact, facts) {
  return facts.find(f => factsAreEqual(fact, f)) !== undefined;
}

function haveSameFacts(factsA, factsB) {
  if (factsA?.length !== factsB?.length) {
    return false;
  }
  return factsA.every(factA => factIsInFacts(factA, factsB));
}

export function personsAreEqual(personA, personB) {
  // We'll assume persons have a single name and nameForm
  if (personA?.names?.length !== personB?.names?.length) {
    return false;
  }
  const namesAreEqual = JSON.stringify(personA?.names[0]?.nameForms[0]) === JSON.stringify(personB.names[0]?.nameForms[0]);
  const sameFacts = haveSameFacts(personA?.facts, personB?.facts);
  // This _should_ only be the Role field.
  const sameFields = personFieldsAreEqual(personA.fields, personB.fields);
  const samePrincipalStatus = personA.principal === personB.principal;
  const sameGender = personA.gender?.type === personB.gender?.type;

  return namesAreEqual && sameFacts && sameFields && samePrincipalStatus && sameGender;
}

export function getPersonsIntersection(leftPersons, rightPersons) {
  return leftPersons.filter(lp => rightPersons.find(rp => personsAreEqual(lp, rp)) !== undefined);
}

export default function PersonsDiff({leftGx, setLeftGx, rightGx, setRightGx, finalGx, setFinalGx, setLeftFilename, setRightFilename}) {
  const [leftHidden, setLeftHidden] = useState(false);
  const [rightHidden, setRightHidden] = useState(false);

  async function handleRightFileDrop(files, setRightGx, setRightHidden) {
    let droppedGxObject = null;
    if (!files || files.length > 1) {
      console.log('Problem reading file, or too many files (max 1)');
    }
    else {
      droppedGxObject = JSON.parse(await files[0].text());
      droppedGxObject = droppedGxObject.records ? droppedGxObject.records[0] : droppedGxObject;
      setRightGx(droppedGxObject);
      setRightFilename(files[0].name);
      setRightHidden(false);
    }
    if (leftGx !== EMPTY_GEDCOMX && rightGx !== EMPTY_GEDCOMX) {
      setFinalGx(getGxIntersection(leftGx, droppedGxObject));
    }
  }

  async function handleLeftFileDrop(files, setLeftGx, setLeftHidden) {
    let droppedGxObject = null;
    if (!files || files.length > 1) {
      console.log('Problem reading file, or too many files (max 1)');
    }
    else {
      droppedGxObject = JSON.parse(await files[0].text());
      droppedGxObject = droppedGxObject.records ? droppedGxObject.records[0] : droppedGxObject;
      setLeftGx(droppedGxObject);
      setLeftFilename(files[0].name);
      setLeftHidden(false);
    }
    if (leftGx !== EMPTY_GEDCOMX && rightGx !== EMPTY_GEDCOMX) {
      setFinalGx(getGxIntersection(droppedGxObject, rightGx));
    }
  }

  const dropZone = (
    <Box sx={{width: '100%', height: '60vh'}}>
      <Typography variant='h3'>
        Drop GedcomX Here
      </Typography>
    </Box>
  );

  return (
    <Grid container alignItems='flex-start' justifyContent='center'>
      <Grid item xs={6}>
        <FileDrop
          onDragOver={() => setLeftHidden(true)}
          onDragLeave={() => setLeftHidden(false)}
          onDrop={files => handleLeftFileDrop(files, setLeftGx, setLeftHidden)}
        >
          {
            leftHidden ? dropZone :
            <RecordsDataContext.Provider value={leftRecordsData(leftGx, setLeftGx, rightGx, setRightGx, finalGx, setFinalGx)}>
              <PersonsList persons={leftGx.persons}/>
            </RecordsDataContext.Provider>
          }
        </FileDrop>
      </Grid>
      <Grid item xs={6}>
        <FileDrop
          onDragOver={() => setRightHidden(true)}
          onDragLeave={() => setRightHidden(false)}
          onDrop={files => handleRightFileDrop(files, setRightGx, setRightHidden)}
        >
          {
            rightHidden ? dropZone :
            <RecordsDataContext.Provider value={rightRecordsData(leftGx, setLeftGx, rightGx, setRightGx, finalGx, setFinalGx)}>
              <PersonsList persons={rightGx.persons}/>
            </RecordsDataContext.Provider>
          }
        </FileDrop>
      </Grid>
    </Grid>
  )
}
