import {useContext, useState} from "react";
import {styled} from "@mui/material/styles";
import {Checkbox, Dialog, FormControlLabel, Grid, MenuItem, Select, TextField, Typography} from "@mui/material";
import {updatePersonsData} from "./EditablePerson";
import {FACT_ATTRIBUTES, FACT_TYPE, GEDCOMX_ROLE_URI} from "../constants";
import {RecordsDataContext} from "../RecordsContext";

const RootContainer = styled('div')(({ theme }) => ({
  marginLeft: theme.spacing(4),
  marginRight: theme.spacing(4),
  marginTop: theme.spacing(4),
  marginBottom: theme.spacing(4),
}));

export default function AddFactOrFieldDialog({open, setOpen, person, personIndex}) {
  const recordsData = useContext(RecordsDataContext);

  const [type, setType] = useState('');
  const [date, setDate] = useState('');
  const [place, setPlace] = useState('');
  const [value, setValue] = useState('');
  const [primary, setPrimary] = useState(false);
  const [role, setRole] = useState('');

  const newFact = {};
  if (type !== '') {
    newFact[FACT_ATTRIBUTES.type] = type;
  }
  if (date !== '') {
    newFact[FACT_ATTRIBUTES.date] = {original: date};
  }
  if (place !== '') {
    newFact[FACT_ATTRIBUTES.place] = {original: place};
  }
  if (value !== '') {
    newFact[FACT_ATTRIBUTES.value] = value;
  }
  newFact[FACT_ATTRIBUTES.primary] = primary;
  const newField = role !== '' ? {type: GEDCOMX_ROLE_URI, values: [{text: role}]} : null;

  function handleDialogClose() {
    setOpen(false);
    // Only add a new fact if it has more than just the 'primary' attribute
    if (Object.keys(newFact).length > 1) {
      person.facts.push(newFact);
    }
    if (newField) {
      person.fields.push(newField);
    }
    updatePersonsData(person, personIndex, recordsData);
    setType('');
    setDate('');
    setPlace('');
    setValue('');
    setPrimary(false);
    setRole('');
  }

  return (
    <Dialog open={open} onClose={handleDialogClose}>
      <RootContainer>
        <Grid container direction='column' spacing={2}>
          <Grid item>
            <Typography>Type</Typography>
            <Select key='type-input' value={type} onChange={e => setType(e.target.value)}>
              {Object.keys(FACT_TYPE).map(t => <MenuItem key={`type-input-${t}`} value={FACT_TYPE[t]}>{t}</MenuItem>)}
            </Select>
          </Grid>
          <Grid item>
            <Typography>Date</Typography>
            <TextField key='date-input' value={date} onChange={e => setDate(e.target.value)}/>
          </Grid>
          <Grid item>
            <Typography>Place</Typography>
            <TextField key='place-input' value={place} onChange={e => setPlace(e.target.value)}/>
          </Grid>
          <Grid item>
            <Typography>Value</Typography>
            <TextField key='value-input' value={value} onChange={e => setValue(e.target.value)}/>
          </Grid>
          <Grid item>
            <FormControlLabel control={<Checkbox checked={primary} onChange={event => setPrimary(event.target.checked)}/>} label={'Primary'}/>
          </Grid>
          <Grid item>
            <Typography>Role</Typography>
            <TextField key='role-input' value={role} onChange={e => setRole(e.target.value)}/>
          </Grid>
        </Grid>
      </RootContainer>
    </Dialog>
  );
}