import React from 'react';
import {Typography} from "@mui/material";
import {styled} from "@mui/material/styles";

const ImageViewerContainer = styled('div')(({ theme }) => ({
  margin: theme.spacing(2),
  flex: 1,
  display: 'flex',
  width: '100%',
  height: '80vh',
  backgroundColor: 'gray',
}));

/**
 * Presents a thin react wrapper over the fs-single-image-viewer component. (Not react)
 *
 * Intended to be used as the base building block when an image viewer is needed.
 * No logic should be added to this component except to expose additional options
 * that are supported by fs-single-image-viewer.
 */
function AceImageViewerV2({
                            /**
                             * Unique id for this component.
                             */
                            id,
                            /**
                             * Index of this image in a panel of multiple images.
                             */
                            idx = 0,
                            /**
                             * Image data to display.
                             */
                            image,
                            /**
                             * Markers (if any) to render over the image. Should conform to the structure
                             * described in https://github.com/fs-webdev/image-viewer/blob/master/API.md#imageviewermarkers-public-functions
                             *
                             * NOTE: The 'node' attribute should NOT be included. Instead, include an 'html' attribute containing the HTML string to use
                             * for the marker. This component will add it to the DOM, and populate the 'node' attribute before giving it to the
                             * fs-single-image-viewer.
                             */
                            markers = [],
                            /**
                             * Callback to be invoked once the image has finished loading. (Visible)
                             */
                            onImageLoad = () => { },
                          }) {

  window[`onAceImageLoad${idx}`] = onImageLoad;

  const viewerRef = React.useRef();

  React.useEffect(function () {
    const reader = new FileReader();
    const viewerElement = viewerRef.current;
    const imageName = image.name;

    reader.onload = () => {
      viewerElement.dataset.filename = imageName;
      viewerElement.src = reader.result;
    };

    reader.onerror = (e) => {
      console.error(e);
    };

    if (image && image.fileObj) {
      reader.readAsDataURL(image.fileObj)
    }

    markers.forEach(marker => {
      marker.node = document.getElementById(marker.id);
    });
  });

  return (
    <div>
      <Typography variant="subtitle1" key={"image-title_" + id} >
        {image.name}
      </Typography>
      <ImageViewerContainer>
        <fs-single-image-viewer
          id={"image-viewer-v2-" + id}
          ref={viewerRef}
          on-image-load={`onAceImageLoad${idx}()`}
        />
      </ImageViewerContainer>
    </div>
  )
}

export default AceImageViewerV2;
