import {useContext, useEffect, useState} from "react";
import {getPersonsIntersection, personsAreEqual} from "./PersonsDiff";
import {Button, Grid, IconButton, List, ListItem, ListItemText, Paper, Tooltip} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import FemaleIcon from '@mui/icons-material/Female';
import MaleIcon from '@mui/icons-material/Male';
import StarIcon from '@mui/icons-material/Star';
import QuestionMarkIcon from '@mui/icons-material/QuestionMark';
import PersonNameEditDialog from "./PersonNameEditDialog";
import EditablePersonFact from "./EditablePersonFact";
import EditablePersonField from "./EditablePersonField";
import AddIcon from "@mui/icons-material/Add";
import AddFactOrFieldDialog from "./AddFactOrFieldDialog";
import {GENDER, NAME_PART_TYPE} from "../constants";
import {RecordsDataContext} from "../RecordsContext";
import {updateRelationshipsData} from "../relationships-diff/RelationshipsDiff";

function getNamePartByType(parts, type) {
  return parts.find(part => part.type === type);
}

function hasMatchingPerson(person, comparingTo) {
  return comparingTo.find(p => personsAreEqual(person, p)) !== undefined;
}

function getFullTextName(nameParts) {
  return [nameParts?.prefix?.value, nameParts?.given?.value, nameParts?.surname?.value, nameParts?.suffix?.value].join(' ').trim();
}

function getNamePartsObject(parts) {
  return {
    prefix: getNamePartByType(parts, NAME_PART_TYPE.prefix),
    given: getNamePartByType(parts, NAME_PART_TYPE.given),
    surname: getNamePartByType(parts, NAME_PART_TYPE.surname),
    suffix: getNamePartByType(parts, NAME_PART_TYPE.suffix)
  };
}

function getGenderIcon(person) {
  if (!person || !person.gender) {
    return <QuestionMarkIcon/>;
  }
  if (person.gender.type === GENDER.Male) {
    return <MaleIcon/>;
  } else if (person.gender.type === GENDER.Female) {
    return <FemaleIcon/>;
  } else {
    return <QuestionMarkIcon/>;
  }
}

function getGenderString(person) {
  if (!person || !person.gender) {
    return 'Unknown';
  }
  if (person.gender.type === GENDER.Male) {
    return 'Male';
  } else if (person.gender.type === GENDER.Female) {
    return 'Female';
  } else {
    return 'Unknown';
  }
}

function getPrincipalIcon(person) {
  if (!person || !person.principal) {
    return <StarIcon sx={{color: '#dadada'}} size='small'/>;
  }
  return <StarIcon/>;
}

function getPrincipalState(person) {
  if (!person || !person.principal) {
    return 'is not principal';
  }
  return 'is principal';
}

export function updateRecordsData(recordsData) {
  const persons = recordsData.gx.persons;
  const comparingTo = recordsData.comparingToGx.persons;

  recordsData.finalGx.persons = getPersonsIntersection(persons, comparingTo);
  recordsData.setFinalGx(structuredClone(recordsData.finalGx));

  recordsData.gx.persons = persons;
  recordsData.setGx(structuredClone(recordsData.gx));
}

export function updatePersonsData(person, personIndex, recordsData) {
  recordsData.gx.persons.splice(personIndex, 1, person);
  updateRecordsData(recordsData);
  updateRelationshipsData(recordsData);
}

export default function EditablePerson({person, index}) {
  const recordsData = useContext(RecordsDataContext);
  const persons = recordsData.gx.persons;
  const comparingTo = recordsData.comparingToGx.persons;

  const parts = person.names[0]?.nameForms[0]?.parts;
  // Compare index to index for persons because person order should be the same
  const nameParts = getNamePartsObject(parts);
  const fullTextName = getFullTextName(nameParts);
  const [hasMatch, setHasMatch] = useState(hasMatchingPerson(person, comparingTo));
  const [isEditingPerson, setIsEditingPerson] = useState(false);
  const [addFactOrField, setAddFactOrField] = useState(false);

  const backgroundColor = hasMatch ? 'white' : '#ffe9e9';
  const textColor = hasMatch ? 'black' : 'red';
  const genderIcon = getGenderIcon(person);
  const principalIcon = getPrincipalIcon(person);

  useEffect(() => {
    setHasMatch(hasMatchingPerson(person, comparingTo));
  }, [person, comparingTo]);

  function handleDeletePerson() {
    persons.splice(index, 1);
    updateRecordsData(recordsData);
  }

  function handleDialogClose(nameParts, person) {
    setIsEditingPerson(false);
    person.names[0].nameForms[0].parts = nameParts;
    person.names[0].nameForms[0].fullText = getFullTextName(getNamePartsObject(nameParts));
    updatePersonsData(person, index, recordsData);
  }

  function handleEdit() {
    setIsEditingPerson(true);
  }

  function handleChangeGender() {
    if (person.gender.type === GENDER.Male) {
      person.gender.type = GENDER.Female;
    } else if (person.gender.type === GENDER.Female) {
      person.gender.type = GENDER.Unknown;
    } else if (person.gender.type === GENDER.Unknown) {
      person.gender.type = GENDER.Male;
    }
    updatePersonsData(person, index, recordsData);
  }

  function handleSetPrincipal() {
    person.principal = person.principal === undefined || person.principal === false;
    updatePersonsData(person, index, recordsData);
  }

  function handleAddFactOrField() {
    setAddFactOrField(true);
  }

  return (
    <>
      <Paper sx={{margin: 2}} square elevation={4}>
        <ListItem key={index} sx={{background: backgroundColor, paddingRight: 0}}>
          <Grid container direction='row' justifyContent='space-between' alignItems='center'>
            <Grid item>
              <ListItemText primary={fullTextName} sx={{color: textColor}} primaryTypographyProps={{fontSize: '20px'}}/>
            </Grid>
            <Grid item>
              <Grid container direction='row' alignItems='center'>
                <Grid item>
                  <Tooltip title={`Gender (${getGenderString(person)})`} arrow>
                    <IconButton onClick={handleChangeGender}>
                      {genderIcon}
                    </IconButton>
                  </Tooltip>
                </Grid>
                <Grid item>
                  <Tooltip title={getPrincipalState(person)} arrow>
                    <IconButton onClick={handleSetPrincipal}>
                      {principalIcon}
                    </IconButton>
                  </Tooltip>
                </Grid>
                <Grid item>
                  <Button onClick={handleEdit}>Edit</Button>
                </Grid>
                <Grid item>
                  <Tooltip title={'Delete Person'} arrow>
                    <IconButton onClick={handleDeletePerson}>
                      <DeleteIcon/>
                    </IconButton>
                  </Tooltip>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <PersonNameEditDialog
            open={isEditingPerson}
            onClose={handleDialogClose}
            nameParts={nameParts}
            person={person}
          />
        </ListItem>
        <List component='div' disablePadding dense>
          {person?.facts?.map((fact, idx) => {
            return (
              <EditablePersonFact
                key={`fact-${idx}`}
                fact={fact}
                factIndex={idx}
                person={person}
                personIndex={index}
              />
            )})}
          {person && person.fields ? person.fields.map((field, idx) => {
            return (
              <EditablePersonField
                key={`field-${idx}`}
                field={field}
                fieldIndex={idx}
                person={person}
                personIndex={index}
              />
            );
          }) : null}
          <Button variant={'outlined'} sx={{margin: 1}} onClick={handleAddFactOrField} startIcon={<AddIcon/>}>
            Add Fact or Role
          </Button>
          <AddFactOrFieldDialog
            open={addFactOrField}
            setOpen={setAddFactOrField}
            person={person}
            personIndex={index}
          />
        </List>
      </Paper>
    </>
  );
}
